        column-gap: 20px;
    }

    div.sidebar_buttons{
        justify-content: center;
        column-gap: 20px;
    }

    img.myPhoto{
        display: none;
    }

    div.aboutRow{
        grid-template-columns: 1fr;
    }
    
    iframe.videoSize{
        height:225px;
        width:400px;
    }


    div.grid_layout {
        grid-template-columns: none;
    }


    text.sidebar_button_text{
        font-size: 25px;
    }

    div.sidebar_button_container{

    }

    h1.project_Title{
        font-size: 24px;
    }

    h1.portfolio_heading{
        font-size: 18px;
    }

    h2.portfolio_heading{
        font-size: 18px;
    }


    text.portfolio_text{
